export default {
	props: {
		// 按钮大小
		size: String,
		// 按钮类型
		type: String,
		// 是否镂空
		plain: Boolean,
		// 是否禁用
		disabled: Boolean,
		// 是否加载中
		loading: Boolean,
		// 是否圆角
		round: Boolean,
		// 是否阴影
		shadow: Boolean,
		// 图标
		icon: String,
		fill: Boolean,
		formType: String,
		openType: String,
		hoverClass: {
			type: String,
			default: 'button-hover'
		},
		hoverStartTime: {
			type: Number,
			default: 20
		},
		hoverStayTime: {
			type: Number,
			default: 70
		},
		appParameter: String,
		hoverStopPropagation: Boolean,
		lang: {
			type: String,
			default: 'en'
		},
		sessionForm: String,
		sendMessageTitle: String,
		sendMessagePath: String,
		sendMessageImg: String,
		showMessageCard: Boolean
	},

	methods: {
		getphonenumber(e) {
			this.$emit('getphonenumber', e);
		},
		getuserinfo(e) {
			this.$emit('getuserinfo', e);
		},
		error(e) {
			this.$emit('error', e);
		},
		opensetting(e) {
			this.$emit('opensetting', e);
		},
		launchapp(e) {
			this.$emit('launchapp', e);
		},
		tap(e) {
			this.$emit('click', e);
		}
	}
};
